<template>
    <v-app id="fondo">
            <template id='filter' >
                <ToolBar/>
            </template>
            <v-content v-bind:style="{'margin-top':'0%'}"> 
                <transition name="slide-fade">
                    <router-view></router-view>
                </transition>
            </v-content>
        <Footer/>
    </v-app>
</template>

<script>
import ToolBar from './toolBar.vue';
import Footer from './footer.vue'

export default {
    name: 'Esquema',
    components: {
        ToolBar,
        Footer
    },
    data() {
        return {
            route: null,
        }
    },
    mounted() {
        this.route = this.$route.name
        this.changeImg()
        
    },
    updated() {
        this.route = this.$route.name
        this.changeImg()
        },
    methods: {
        changeImg(){
            
            document.getElementById("fondo").style.backgroundRepeat = 'no-repeat';
            document.getElementById("fondo").style.backgroundSize = '100%';
            // document.getElementById("fondo").style.backgroundColor = '#02539e8f';
            if(this.route == 'Qhacemos'){
                document.getElementById("fondo").style.backgroundImage = 'url(' + require('../assets/fondoNosotros.jpg') + ')';
            }
            if(this.route == 'Productos'){
                document.getElementById("fondo").style.backgroundImage = 'url(' + require('../assets/fondoProductos.jpg') + ')';
            }
             if(this.route == 'Inicio'){
                document.getElementById("fondo").style.backgroundImage = 'url(' + require('../assets/fondo1.jpg') + ')';
            }
            if(this.route == 'Qsomos'){
                document.getElementById("fondo").style.backgroundImage = 'url(' + require('../assets/fondoSomos.jpg') + ')';
            }
            if(this.route == 'Equipo'){
                document.getElementById("fondo").style.backgroundImage = 'url(' + require('../assets/fondoEquipo.jpg') + ')';
            }
            if(this.route == 'Contacto'){
                document.getElementById("fondo").style.backgroundImage = 'url(' + require('../assets/fondoContacto.jpg') + ')';
            }
        }
        
    },
}
</script>

<style>
#fondo{
    background-color: #0a1d45
}
#filter{
    background-color: #02539e8f !important;
    position: absolute !important;
