<template>
    <v-row v-bind:style="{'margin-top': '2%'}">
        <v-col cols='12' md='5'>
            <v-row>
                <v-col  v-for="data in categories" v-model="categoria" :key="data">
                    <v-btn
                        plain
                        @click="getAliados(data)"
                        class="btnBig">
                        <v-container  v-bind:style="{'border-bottom' : '2px solid white', 'width': '450px', 'text-align': 'initial', 'padding-bottom': '0%'}">
                            {{data}}
                        </v-container>
                        <v-icon
                            color="white"
                            large
                            >
                            mdi-play
                        </v-icon>
                    </v-btn>
                </v-col>
            </v-row>
        </v-col>
        <v-col v-if="noRow != 0 && noCol != 0" cols='12' md='7'>
            <v-row v-for="dataR in noRow" :key="dataR" >
                <v-col  v-bind:style="{'text-align': 'center !important' }" v-for="dataC,i  in noCol" :key="dataC" >
                    <v-avatar
                        
                        size="150">
                            <transition name="fade">
                                    <img 
                                    alt="aliados CB"
                                    v-bind:src=aliadosByC[i]
                                    >
                            </transition>
                    </v-avatar>
                </v-col>
            </v-row>            
        </v-col>
    </v-row>
</template>

<script>

export default {
    data() {
        return {
            categories: ['GOBIERNO','CIENCIA Y TECNOLOGÍA','EDUCACIÓN Y FORMACIÓN', 'SALUD, BIENESTAR Y BELLEZA' ], 
            aliados: [{ nombre: 'SALUD, BIENESTAR Y BELLEZA', data: ["https://pbs.twimg.com/profile_images/1371957609411002373/uv-resvo_400x400.jpg", require('../../assets/Logos/aseoServicios.jpg'), "data:image/png;base64,", "https://scontent.fbga1-4.fna.fbcdn.net/v/t1.6435-9/161327296_1111828772579545_1688246364689778038_n.jpg?_nc_cat=105&ccb=1-5&_nc_sid=09cbfe&_nc_eui2=AeFuFQM7gW3VtMpqC4Wlh51AMNkcyVRvQKIw2RzJVG9AolPnpZUoWHgmApFHG9Ih2Ck&_nc_ohc=6RaJ3SQljxUAX9O-1Sh&_nc_ht=scontent.fbga1-4.fna&oh=786643026ba9c6a36e39ceebf36ac8b0&oe=6154397F"]},
                    {nombre :'GOBIERNO', data: ["data:image/png;base64,", require('../../assets/Logos/minTic.jpg')]},
                    {nombre: 'CIENCIA Y TECNOLOGÍA' , data: [require('../../assets/Logos/suricataLabs.jpg'), require('../../assets/Logos/absoluta.jpg')]},
                    {nombre : 'EDUCACIÓN Y FORMACIÓN', data: [require('../../assets/Logos/eduExpert.jpeg')]}],
            noRow: 0,
            noCol:0, 
            categoria: null,
            aliadosByC: null,
        }
    },
    methods: {
        getAliados(data){
            this.aliados.forEach((element) => {
                if(element.nombre == data){
                    this.noRow = 0
                    this.noCol = 0
                    this.aliadosByC = element.data 
                    this.noRow = parseInt(this.aliadosByC.length/4)
                    if(this.noRow == 0){
                        this.noRow = 1;
                    }
                    this.noCol = this.aliadosByC.length
                }
            });
            
        }
    },

}
</script>
